import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import key from "../../util/key";
import {
  SearchOutlined,
  UnorderedListOutlined,
} from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import {Modal} from "antd";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import Flex from "../../util/Flex";
import InvoiceTransactions from "../Invoice/InvoiceTransactions";
import InvoiceEditModal from "./InvoiceEditModal";

class ClientInvoice extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
      editModalVisible: false,
      editModalId: null,
      showTransactions: false,
      currentItem: {},
    };
  }
  m = (key, prefix = "invoice.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  editModalToggle = (editModalId = null) => {
    this.setState((state) => {
      return {editModalVisible: !state.editModalVisible, editModalId};
    });
  };

  editModalFinish = () => {
    this.editModalToggle();
    this.onMethodFinish();
  };

  showTransactions = (currentItem) => {
    this.setState({ currentItem, showTransactions: true });
  };

  closeTransactions = () => {
    this.setState({ showTransactions: false });
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          text: true,
          title: this.m("type"),
          dataIndex: "typename",
          sorter: true,
          width: "15%",
          render: (value, item) => <>
            <div>{item.typename}</div>

            {item.typedescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.typedescription}
              </div>
            )} </>,
        },
        {
          text: true,
          title: this.m("code"),
          dataIndex: "code",
          sorter: true,
          width: "15%",

          render: (value, item) => <>
            <div>{value}</div>

            {item.description && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.description}
              </div>
            )} </>,

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("currency"),
          dataIndex: "currencycode",
          sorter: true,
          width: "15%",
          render: (value, item) => <>
            <div>{item.currencycode} ({item.currencyname})</div>

            {item.currencydescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.currencydescription}
              </div>
            )} </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          title: this.m("amount"),
          dataIndex: "amount",
          sorter: true,
          width: "10%",
          render: (value, item) => (
            <Flex between>
              {value}
              <span
                className="gx-ml-2 gx-link"
                onClick={() => this.showTransactions(item)}
              >
                <UnorderedListOutlined/>
              </span>
            </Flex>
          ),
        },
        {
          title: this.m("created"),
          dataIndex: "created",
          defaultSortOrder: true,
          sortDirections: "descend",
          sorter: true,
          width: "10%",
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br/>
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          width: "10%",
          render: (value, item) => (
            <div className="gx-text-right">
              <Statelabel onFinish={this.onMethodFinish} {...item} />
            </div>
          ),
        },
      ],
    });
  };

  render() {
    const forceFilter = this.props.clientId ? { client: this.props.clientId } : null;
    return (
      <div>
        <InvoiceEditModal
          essence="invoice"
          client={this.props.clientId}
          onClose={this.editModalToggle}
          onFinish={this.editModalFinish}
          visible={this.state.editModalVisible}
          id={this.state.editModalId}
        />

        <Modal
          open={this.state.showTransactions}
          footer={null}
          onCancel={this.closeTransactions}
        >
          <InvoiceTransactions item={this.state.currentItem} />
        </Modal>

        <DataTable
          forceFilter={forceFilter}
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/invoice/list"}
        />
      </div>
    );
  }
}

export default injectIntl(ClientInvoice);
