import React from "react";
import PlainSelect from "../../util/PlainSelect";
import {Form, Input} from "antd";

const {TextArea} = Input;
// let admin = false;
// const Check = () => {
//
//   const user = useSelector(({user}) => user);
//   admin = user.isAdmin ? true : false;
// }
export default function clientEditForm() {
  //const config = window.appConfig || {};

  let formItems = {
    type: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("typename"),
      component: (
        <PlainSelect labelKey="name" cache query="/client/type"/>
      ),
    },
    code: {
      rules: [
        {
          required: true,
        },
      ],
    },
    firstname: {
      rules: [
        {
          required: false,
        },
      ],
    },
    lastname: {
      rules: [
        {
          required: false,
        },
      ],
    },
    middlename: {
      rules: [
        {
          required: false,
        },
      ],
    },
    email: {
      rules: [
        {
          required: false,
        },
      ],
    },
    phone: {
      rules: [
        {
          required: false,
        },
      ],
    },
    description: {
      component: <TextArea/>,
    },
  };


  return (
    <>
      {Object.entries(formItems).map(([key, item]) => {
        let label = item.label || this.m(key);
        let component = item.component || <Input/>;

        return (
          <Form.Item key={key} rules={item.rules} label={label} name={key}>
            {component}
          </Form.Item>
        );
      })}
    </>
  );
}
