import React from "react";
import Context from "../../Context";
import {injectIntl} from "react-intl";
import key from "../../util/key";
import {PlusOutlined, SearchOutlined} from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import {Button} from "antd";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import OrderEditModal from "./OrderEditModal";
import {Link} from "react-router-dom";

class ClientOrder extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],

      editModalVisible: false,
      editModalId: null,
    };
  }

  m = (key, prefix = "order.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  editModalToggle = (editModalId = null) => {
    this.setState((state) => {
      return {editModalVisible: !state.editModalVisible, editModalId};
    });
  };

  editModalFinish = () => {
    this.editModalToggle();
    this.onMethodFinish();
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          text: true,
          title: (<>
            {this.props.access ?
              (<Button
                className="gx-mr-4"
                onClick={(e) => {
                  e.stopPropagation();
                  this.editModalToggle();
                }}
                icon={<PlusOutlined/>}
              />) : (<></>)
            }{this.m("code")}
          </>),
          dataIndex: "code",
          sorter: true,
          width: "10%",

          render: (value, item) => <>
            <div>{value}</div>

            {item.description && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.description}
              </div>
            )} </>,

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("debitcode"),
          dataIndex: "debitcode",
          sorter: true,
          width: "10%",
          render: (value, item) => <>
            <div style={{whiteSpace: "nowrap"}}>
              {value}
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.debitlabel}
              </div>
            </div>
          </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("payername"),
          dataIndex: "payername",
          sorter: true,
          width: "10%",
          render: (value, item) => (
            <div className="gx-flex-column gx-align-items-left">
              <Link to={`/crm/clients/${item.payer}`}>
                <div>{value}</div>
              </Link>

              {item.payercode && (
                <div className="gx-font-weight-light gx-text-small gx-text-muted">
                  {item.payercode}
                </div>
              )}
            </div>
          ),
        },
        {
          text: true,
          title: this.m("creditcode"),
          dataIndex: "creditcode",
          sorter: true,
          width: "15%",
          render: (value, item) => <>
            <div style={{whiteSpace: "nowrap"}}>
              {value}
            </div>
            <div className="gx-font-weight-light gx-text-small gx-text-muted">
              {item.creditlabel}
            </div>
          </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("payeename"),
          dataIndex: "payeename",
          sorter: true,
          width: "10%",
          render: (value, item) => (
            <div className="gx-flex-column gx-align-items-left">
              <Link to={`/crm/clients/${item.payee}`}>
                <div>{value}</div>
              </Link>

              {item.payeecode && (
                <div className="gx-font-weight-light gx-text-small gx-text-muted">
                  {item.payeecode}
                </div>
              )}
            </div>
          ),
        },
        {
          text: true,
          title: this.m("currency"),
          dataIndex: "currencycode",
          sorter: true,
          width: "15%",
          render: (value, item) => <>
            <div>{item.currencycode} ({item.currencyname})</div>

            {item.currencydescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.currencydescription}
              </div>
            )} </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          title: this.m("amount"),
          dataIndex: "amount",
          sorter: true,
          width: "10%",
        },
        {
          title: this.m("created"),
          dataIndex: "created",
          defaultSortOrder: true,
          sortDirections: "descend",
          sorter: true,
          width: "10%",
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br/>
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          width: "10%",
          render: (value, item) => (
            <div className="gx-text-right">
              <Statelabel onFinish={this.onMethodFinish} {...item} />
            </div>
          ),
        },
      ],
    });
  };

  render() {
    return (
      <div>
        <OrderEditModal
          essence="order"
          onClose={this.editModalToggle}
          onFinish={this.editModalFinish}
          visible={this.state.editModalVisible}
          id={this.state.editModalId}
        />

        <DataTable
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          forceFilter={{client: this.props.clientId}}
          path={"/order/list"}
        />
      </div>
    );
  }
}

export default injectIntl(ClientOrder);
