import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import { Button, Form, Input, message, Modal, Spin } from "antd";
import NumericInput from "../../util/NumericInput";
import PlainSelect from "../../util/PlainSelect";
const { TextArea } = Input;

class InvoiceEditModal extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      pending: false,
    };
  }

  m = (key, prefix = "invoice.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };

  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  onClose = () => {
    if (this.props.onClose) this.props.onClose();
  };

  save = async (data) => {
    this.pendingStart();
    let result = await this.context.api.q("/invoice/set", {
      client: this.props.client,
      currency: data.currency,
      amount: data.amount,
      description: data.description,
    });
    if (!result) {
      message.error(this.m("save.fail", ""));
    } else {
      message.success(this.m("save.success", ""));
      if (this.props.onFinish) this.props.onFinish();
    }
    this.pendingEnd();
  };

  render() {
    const { visible, client } = this.props;
    const { pending } = this.state;
    let formItems = {
      currency: {
        rules: [
          {
            required: true,
          },
        ],
        label: this.m("currency"),
        component: (
          <PlainSelect labelKey="label" cache query="/currency/list" />
        ),
      },
      amount: {
        rules: [
          {
            required: true,
          },
        ],
        component: ( <NumericInput/>),
      },
      description: {
        component: <TextArea />,
      },
    };

    return (
      <Modal open={visible} footer={null} onCancel={this.onClose}>
        <Spin spinning={pending}>
          <Form
            key={client}
            layout="vertical"
            initialValues={{

            }}
            onFinish={this.save}
            scrollToFirstError
          >
            {Object.entries(formItems).map(([key, item]) => {
              let label = item.label || this.m(key);
              let component = item.component || <Input />;

              return (
                <Form.Item key={key} rules={item.rules} label={label} name={key}>
                  {component}
                </Form.Item>
              );
            })}
            <Button
              loading={pending}
              type="primary"
              htmlType="submit"
              className="gx-mb-0"
            >
              {this.m("save", "")}
            </Button>
          </Form>
        </Spin>
      </Modal>
    );
  }
}

export default injectIntl(InvoiceEditModal);
